import React from "react";
import ComMenu from "@/components/comMenu/ComMenu.jsx";

import { useTranslation } from "react-i18next";
export default function UserCenter(props) {
  const { t } = useTranslation();
  const menus = [
    {
      title: t("support.workOrder"),
      to: "ticket",
    },
    {
      title: t("support.help"),
      to: "help",
    },
    {
      title: t("support.notice"),
      to: "notice",
    },
    {
      title: t("support.fileDownload"),
      to: "download",
    },
  ];

  return (
    <>
      <ComMenu menus={menus}></ComMenu>
    </>
  );
}
